<template>
  <div class="bussiness">
    <div class="toolBar">
      <div class="datapicker">
        <el-date-picker
          v-model="dataRange"
          size="small"
          type="daterange"
          align="right"
          range-separator=""
          start-placeholder=""
          end-placeholder=""
          :picker-options="pickerOptions">
        </el-date-picker>
        <el-button size="mini" type="primary"></el-button>
      </div>
      <div class="dataStatistics">
        <databar :dataArray="dataArray"></databar>
      </div>
      <div class="charts">
        <div ref="businessChart" class="charts-item"></div>
      </div>
      <div class="bussinessTable">
        <el-table
          :data="tableData"
          style="width: 100%">
          <el-table-column
            prop="date"
            label="">
          </el-table-column>
          <el-table-column
            prop="amount"
            label="">
          </el-table-column>
          <el-table-column
            prop="count"
            label="">
          </el-table-column>
          <el-table-column
            prop="average"
            label="">
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import databar from '../common/databar'
  import echarts from 'echarts'

  export default {
    data() {
      return {
        pickerOptions: {
          shortcuts: [{
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        dataRange: '',
        dataArray: [
          {
            name: '',
            data: '18875'
          },
          {
            name: '',
            data: 1099
          },
          {
            name: '',
            data: "18.98"
          },
          {
            name: '',
            data: "98%"
          },
          {
            name: '',
            data: "88%"
          }
        ],
        chart: null,
        tableData: []
      };
    },
    mounted() {
      this.requestData();
      this.initChart();
    },
    methods: {
      initChart() {
        this.chart = echarts.init(this.$refs.businessChart);
        this.chart.setOption({
          title: {
            text: '',
            subtext: ''
          },
          tooltip: {
            trigger: 'axis'
          },
          legend: {
            data:['','']
          },
          toolbox: {
            show: true,
            feature: {
              magicType: {type: ['line', 'bar']},
              restore: {},
              saveAsImage: {}
            }
          },
          xAxis:  {
            type: 'category',
            boundaryGap: false,
            data: ['','','','','','','']
          },
          yAxis: {
            type: 'value',
            axisLabel: {
              formatter: '{value}'
            }
          },
          series: [
            {
              name:'',
              type:'line',
              data:[1134, 1572, 2551, 3323, 1982, 2761, 3394],
              markLine: {
                data: [
                  {type: 'average', name: ''}
                ]
              }
            },
            {
              name:'',
              type:'line',
              data:[251, 221, 353, 162, 331, 553, 251],
              markLine: {
                data: [
                  {type: 'average', name: ''}
                ]
              }
            }
          ]
        })
      },
      requestData() {
        this.$http.get('/static/bussiness.json').then(response => {
        // get body data
        let data = response.body;
        this.tableData = data.reverse();
        }, response => {
          // error callback
        })
      }
    },
    components: {
      databar
    }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>

  .bussiness
    margin 1.5rem 0 0 1.5rem
    .charts
      margin 0 1.5rem 0 1.5rem
      .charts-item
        width 100%
        height 20rem
    .bussinessTable
      margin 0 1.5rem 0 1.5rem
</style>



